@mixin font-size-default($size: 14) {
    $lineHeight: $size + 4;
    font-size: $size + px;
    line-height: $lineHeight + px;
}

@mixin mobile-block() {
    @media screen and (max-width: 768px) {
        width: 100%;
    }
}

@mixin multi-line ($num) {
    word-break: break-all;
    display: -webkit-box;
    -webkit-line-clamp: $num;
    -webkit-box-orient: vertical;
    overflow: hidden;
}

@mixin single-line {
    overflow: hidden;
    text-overflow: ellipsis;
    -o-text-overflow: ellipsis;
    -webkit-text-overflow: ellipsis;
    -moz-text-overflow: ellipsis;
    white-space: nowrap;
}
.card {
  padding: 8px 30px;
  background: #ffffff;
  display: flex;
  flex-direction: column;
  position: relative;
  margin-bottom: 24px;
  border-radius: var(--block-radius);

  &:hover {
    background-color: #fafafa;
  }

  &>a {
    color: #333;
    display: flex;
  }

  .repo-pic {
    margin-right: 12px;
    border-radius: 2px;
    width: 170px;
    height: 114px;
    object-fit: cover;
    object-position: center;
  }
  .repo-header-wrap {
    flex: 1;
    overflow: hidden;
  }
  .repo-header {
    flex: 1;
    display: flex;
    align-items: center;

    .repo-type {
      color: rgb(183, 30, 215);
      margin-right: 2px;
    }

    .repo-title {
      font-size: 18px;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      cursor: pointer;
      font-weight: 400;
    }
  }

  .repo-desc {
    color: #666;
    @include multi-line(2);
  }

  .repo-meta {
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: #999;

    .info-block {
      margin: 0 8px;
      font-size: 12px;
      margin: 0;

      span {
        margin-left: 4px;
      }
    }
  }

  .actions {
    position: absolute;
    top: 4px;
    right: 24px;

    a {
      font-size: 12px;
      margin: 0 8px;
    }
  }

}